@media print {
  html {
    --highlight-color: black;
    --hover-color: black;
    --background-color: white;
    --main-color: black;
  }

  .body-header {
    margin-top: 0;
  }

  .body-aside-content,
  .contacts-links-not-printed,
  .description-details {
    display: none;
  }

  .title-contacts {
    flex-direction: row;
    gap: 10px;
  }

  .title-contacts-email,
  .title-contacts-divider {
    display: block;
  }

  .history-tech-stack span {
    text-decoration: none!important;
  }
}
